<template>
  <div class="index-bg">
    <HeadTop :title="$t('index.ART chain')"></HeadTop>
    <div class="p15">
      <van-swipe :autoplay="3000" lazy-render>
        <van-swipe-item v-for="item in images" :key="item">
          <img class="swiper-img" :src="item" />
        </van-swipe-item>
      </van-swipe>
      <div class="pool-list">
        <div class="pool-item flex-between border-1px-b">
          <div class="pool-left flex-middle">
            <img src="@/assets/images/balance.png">
            <span>{{$t('index.余额')}}</span>
          </div>
          <div class="price">{{Number(balance / Math.pow(10, 18)).toFixed(4)}}</div>
        </div>
        <!-- <div class="pool-item flex-between border-1px-b">
          <div class="pool-left flex-middle">
            <img src="@/assets/images/capital.png">
            <span>{{$t('index.总资金池')}}</span>
          </div>
          <div class="all price"><span>$</span>0</div>
        </div> -->
        <div class="pool-item flex-between border-1px-b">
          <div class="pool-left flex-middle">
            <img src="@/assets/images/art-price.png">
            <span>{{$t('index.ART价格')}}</span>
          </div>
          <div class="price"><span>$</span>{{tokenUsdtPrice}}</div>
        </div>
        <div class="pool-item flex-between border-1px-b">
          <div class="pool-left flex-middle">
            <img src="@/assets/images/flowcell.png">
            <span>{{$t('index.总ART流动池')}}</span>
          </div>
          <div class="price">{{tokenPool}}</div>
        </div>
        <div class="pool-item flex-between border-1px-b">
          <div class="pool-left flex-middle">
            <img src="@/assets/images/reword.png">
            <span>{{$t('index.总BNB流动池')}}</span>
          </div>
          <div class="price">{{wethPool}}</div>
        </div>
        <div class="pool-btn flex-between">
          <van-button @click="toSwap" type="primary">{{$t('index.购买ART')}}</van-button>
          <van-button @click="toKline" plain type="primary">{{$t('index.K线行情')}}</van-button>
        </div>
      </div>
      <div class="index-img">
        <img src="@/assets/images/quan.jpg" />
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import ArtAbi from "@/utils/ArtAbi";
import tokenAbi from "@/utils/abi";
import swapAbi from '@/utils/swapAbi';
import factoryAbi from '@/utils/factoryAbi';
import pairAbi from '@/utils/pairAbi';
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      artContract: null,
      artTokenContract: null,
      routerContract: null,
      balance: 0,
      artPrice: '0.000',
      tokenUsdtPrice: '0.0000',
      tokenPool: '0.0000',
      wethPool: '0.0000',
      images:{
        0: require("@/assets/images/swiper.jpg"),
        1: require("@/assets/images/swiper2.jpg")
      },
      initContract: () => {
        data.artContract = new data.web3.eth.Contract(ArtAbi, config.ArtAddress)
        data.artTokenContract = new data.web3.eth.Contract(tokenAbi, config.TokenAddress)
        data.routerContract = new data.web3.eth.Contract(swapAbi, config.SwapRouter)
        data.initData()
      },
      initData: () => {
        data.getBalance()
        data.getPrice()
        data.getReverse()
        // data.getData()
      },
      getBalance: async () => {
        let balance = await data.artTokenContract.methods.balanceOf(data.address).call()
        data.balance = balance
      },
      getPrice: async () => {
        let address = await data.artContract.methods.usdtAddress().call()
        let usdtToken = new data.web3.eth.Contract(tokenAbi, address)
        let decimals = await usdtToken.methods.decimals().call()
        let amount = await data.routerContract.methods.getAmountsOut('1000000000000000000', [config.TokenAddress, config.WBNB ,address]).call()
        data.tokenUsdtPrice = Number(amount[2]/Math.pow(10, 18)).toFixed(4)
      },
      getReverse: async () => {
        let WETH = await data.routerContract.methods.WETH().call()
        let factory = new data.web3.eth.Contract(factoryAbi, config.FactoryAddress)
        let pair = await factory.methods.getPair(WETH, config.TokenAddress).call()
        let pairContract = new data.web3.eth.Contract(pairAbi, pair)
        let token0 = await pairContract.methods.token0().call()
        let reserves = await pairContract.methods.getReserves().call()
        console.log(reserves)
        if(token0.toLowerCase() == WETH.toLowerCase()) {
          data.tokenPool = Number(reserves._reserve1/Math.pow(10, 18)).toFixed(4)
          data.wethPool = Number(reserves._reserve0/Math.pow(10, 18)).toFixed(4)
        } else {
          data.tokenPool = Number(reserves._reserve0/Math.pow(10, 18)).toFixed(4)
          data.wethPool = Number(reserves._reserve1/Math.pow(10, 18)).toFixed(4)
        }
      },
      toSwap: () => {
        let url = `https://pancakeswap.finance/swap?outputCurrency=${config.TokenAddress}`
        window.location.href = url

      },
      toKline: () => {
        window.location.href = 'https://dex.guru/token/0xe97364f13fc8f40b0ab372a51b645a648f091483-bsc'
        // window.location.href = 'https://pancakeswap.info/'
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
  .van-swipe{
    height: 150px;
    .swiper-img{
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
    .van-swipe__indicator{
      width: 4px;
      height: 2px;
      color: #A6ABD1;
      border-radius: 2px;
    }
    .van-swipe__indicator--active{
      width: 15px;
      height: 2px;
      background-color: #fff;
      border-radius: 5px;
    }
  }
</style>
<style lang="less" scoped>
  .index-bg{
    background: url(~@/assets/images/index-bg.png) no-repeat center top;
    background-size: 100% auto;
    .pool-list{
      background: #FFFFFF;
      border-radius: 10px;
      padding: 0 15px 27px;
      margin: 15px 0;
      .pool-item{
        padding: 17px 0;
        .pool-left{
          width: auto;
          height: 31px;
          img{
            width: auto;
            height: 100%;
          }
          span{
            font-size: 14px;
            color: #333333;
            margin-left: 11px;
          }
        }
        .price{
          font-size: 16px;
          font-weight: bold;
          color: #111111;
          span{
            margin-right: 5px;
          }
        }
        .all{
          color: #0066EE;
        }
      }
      .pool-btn{
        margin-top: 30px;
      }
    }
    .index-img{
      width: 100%;
      height: 80px;
      img{
        width: 100%;
        height: 100%;
        border-radius: 10px;
      }
    }
  }
</style>
